.login-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: var(--color-background);

  .login-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;

      input {
        margin-bottom: 10px;
      }

      .buttons {
        width: 350px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        button {
          margin-bottom: 10px;
        }
      }
    }
  }

  .copy {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    bottom: 25px;
    opacity: 0.4;
  }
}
