@import url('https://fonts.googleapis.com/css?family=Inter:300,400,500,600,700,800&display=swap');
@import './variables.scss';

body,
#root,
html {
  margin: 0;
  font-family: $font-family;
  font-weight: $font-weight-normal;
  font-size: $font-size-m;
  color: var(--color-text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  margin: 0;
  top: 0;
  position: absolute;
  padding: env(safe-area-inset-top) 0 0;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family;
  font-weight: $font-weight-bold;
  margin-top: unset;
}

input:not([type]),
input[type='text'],
input[type='password'],
input[type='email'],
input[type='number'],
textarea,
input[type='checkbox'] {
  padding: 10px;
  font-size: $font-size-m;
  font-family: $font-family;
  max-width: 350px;
  width: 100%;
  border: 1px solid $color-gray;
  border-radius: 4px;
  background-color: $color-background-alt;
  color: $color-primary;
  margin-top: 2px;
  margin-bottom: 2px;
  font-weight: $font-weight-normal;

  &:disabled {
    border-color: $color-light-gray;
    color: $color-gray;
  }

  &:focus {
    outline: 2px solid $color-primary;
  }

  &.large {
    font-size: $font-size-xl;
    border: unset;
    border-bottom: 1px solid var(--color-background-alt-lighter);
    outline: unset;
    padding: 16px;
    font-weight: $font-weight-bold;
    color: $color-primary;

    &:focus,
    &:active {
      color: $color-primary;
      border-bottom-color: $color-primary;
    }
  }
}

button {
  padding: 10px 20px;
  min-width: 150px;
  border: none;
  font-family: $font-family;
  border-radius: 4px;
  background-color: var(--color-primary);
  color: $color-white;

  i.icon {
    margin-right: 8px;
  }

  &:hover {
    background-color: var(--color-primary-light);
    cursor: pointer;
  }

  &:disabled {
    background-color: var(--color-primary-dark);
    color: $color-text;
    cursor: not-allowed;
  }

  &.secondary {
    background-color: var(--color-background-lighter);

    &:hover {
      background-color: var(--color-background-lightest);
    }

    &:disabled {
      background-color: var(--color-background-light);
    }
  }

  &.warning {
    background-color: var(--color-warning);
    color: #fff;
  }

  &.danger {
    background-color: var(--color-red);
    color: #fff;
  }
}

.bold {
  font-weight: var(--font-weight-bold);
}

.info-box {
  max-width: 350px;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 20px;

  .header,
  .footer {
    padding: 10px 15px;
    background-color: var(--color-information);
    color: #fff;

    span {
      font-weight: var(--font-weight-bold);
    }

    i {
      margin-right: 8px;
    }
  }

  .content {
    padding: 10px 15px;
    background-color: var(--color-information-dark);
    color: #fff;
    p {
      margin-top: unset;

      &:last-of-type {
        margin-bottom: unset;
      }
    }
  }

  &.warning {
    .header,
    .footer {
      background-color: var(--color-warning);
      color: var(--color-text-dark);
    }

    .content {
      background-color: var(--color-warning-light);
      color: var(--color-text-dark);
    }
  }
}
